import { GlobalVote } from "../../types/GlobalVote"

export const createVotePercentage = (
  boy: GlobalVote["boy"],
  girl: GlobalVote["girl"],
) => {
  const boyPercentage = Math.floor((boy * 100) / (boy + girl))
  const girlPercentage = Math.floor((girl * 100) / (boy + girl))
  return {
    boyPercentage: boyPercentage ? boyPercentage : 0,
    girlPercentage: girlPercentage ? girlPercentage : 0,
  }
}
