import {
  FormControlLabel,
  Grid,
  Radio,
  RadioGroup,
  Typography,
  makeStyles,
} from "@material-ui/core"
import React from "react"
import { createVotePercentage } from "../../utils/createVotePercentage"
import { useGetVotes } from "../../utils/useGetVotes"
import { useUpdateMetaData } from "../../utils/useUpdateMetaData"

const useStyles = makeStyles({
  percentLosing: {
    color: "red",
    paddingLeft: 8,
  },
  percentWinning: {
    color: "green",
    paddingLeft: 8,
  },
})

export const Vote = () => {
  const classes = useStyles()
  const { boyValue, girlValue } = useGetVotes()
  const { updateMetaData } = useUpdateMetaData()
  const { girlPercentage, boyPercentage } = createVotePercentage(
    boyValue,
    girlValue,
  )
  return (
    <Grid container direction="column" style={{ paddingTop: 24 }}>
      <Typography>Vote Below:</Typography>
      <RadioGroup>
        <FormControlLabel
          value="girl"
          control={
            <Radio onClick={() => updateMetaData(boyValue, girlValue + 1)} />
          }
          label={
            <Grid container direction="row">
              <Typography>Girl</Typography>
              <Typography
                className={
                  girlValue < boyValue
                    ? classes.percentLosing
                    : classes.percentWinning
                }
              >
                {`${girlPercentage}%`}
              </Typography>
            </Grid>
          }
        />
        <FormControlLabel
          value="boy"
          control={
            <Radio onClick={() => updateMetaData(boyValue + 1, girlValue)} />
          }
          label={
            <Grid container direction="row">
              <Typography>Boy</Typography>
              <Typography
                className={
                  boyValue < girlValue
                    ? classes.percentLosing
                    : classes.percentWinning
                }
              >
                {`${boyPercentage}%`}
              </Typography>
            </Grid>
          }
        />
      </RadioGroup>
    </Grid>
  )
}
